import { FinchQueryOptions } from '@finch-graphql/types';
import { DocumentNode, GraphQLFormattedError } from 'graphql';

import {
  GetTabInfoDocument,
  GetTabInfoQuery,
  GetTabInfoQueryVariables,
  InjectIntoPageDocument,
  InjectIntoPageMutation,
  InjectIntoPageMutationVariables,
} from '~/schema/background';

import CurrentDomainHelpers from './helpers/currentDomainHelpers';
import { BackgroundMessageKey } from './types/messages';

type UniversalQueryMethod = <Query extends {}, Variables extends {}>(
  document: DocumentNode,
  variables: Variables,
  options: FinchQueryOptions,
) => Promise<{ data: Query; errors?: GraphQLFormattedError[] }>;

/**
 * maybeInjectContentScript is a function that is called from both the background script
 * and the bootstrap script. This function gets passed a proxy function for querying the api
 * so on the background script it can be queried directly where on the bootstrap script the
 * universalQuery function will have to go through messaging.
 *
 * Both background script and bootstrap scripts will call inject content script if the content
 * script should be injected.
 * @param universalQuery is a function that is able to query the api.
 */
export const maybeInjectContentScript = async (universalQuery: UniversalQueryMethod, tabId?: number) => {
  try {
    const domainHost = await CurrentDomainHelpers.getCurrentDomainHost();
    const { currentDomain, pathName } = domainHost ?? {};

    if (currentDomain) {
      const { data, errors } = await universalQuery<GetTabInfoQuery, GetTabInfoQueryVariables>(
        GetTabInfoDocument,
        {
          domain: currentDomain,
          tabId,
        },
        {
          messageKey: BackgroundMessageKey.Generic,
        },
      );

      if (errors && errors.length) {
        return;
      }

      const windowType = data?.browser?.tab.windowType ?? 'normal';
      const isExtensionEnabled = data?.settings.isGloballyEnabled ?? true;
      const isBanned = data?.settings.domain?.isBanned ?? false;
      const showReminder = data?.settings.domain?.showReminder ?? false;
      const bannedWithNoReminder = isBanned && !showReminder;
      const isToucanInjected = data?.browser?.tab.isToucanInjected ?? false;
      const isToucanSite =
        currentDomain === 'stage-internal.jointoucan.com' ||
        currentDomain === 'localhost' ||
        currentDomain === 'jointoucan.com' ||
        browser.runtime.getURL('/').includes(currentDomain);
      const ToucanPathsThatNeedContentScript = ['/dashboard/settings', '/doctor'];
      const isToucanPageThatDoesNotNeedContentScript =
        isToucanSite && !ToucanPathsThatNeedContentScript.includes(pathName ?? '');

      if (
        !isToucanInjected &&
        !isToucanPageThatDoesNotNeedContentScript &&
        windowType === 'normal' &&
        isExtensionEnabled &&
        !bannedWithNoReminder
      ) {
        try {
          // Inject content script into page.
          await universalQuery<InjectIntoPageMutation, InjectIntoPageMutationVariables>(
            InjectIntoPageDocument,
            tabId ? { input: { tabId } } : {},
            {
              messageKey: BackgroundMessageKey.Generic,
            },
          );
        } catch (e) {
          // NOTE: Ignoring here because there might be permission issues
        }
      }
    }
  } catch (e) {
    // NOTE: just do not do anything on error
  }
};
