import { maybeInjectContentScript } from './content-injection';
import { client } from './finch-client';

/**
 * Bootstrap script allows use to add in a small script on page load and evaluate if we should inject
 * the full content script into the page. This can also be used to be able to setup external messaging.
 */
(async function main() {
  const queryApi = client.query.bind(client);
  await maybeInjectContentScript(queryApi);
})();
